import { useContext, useEffect, useState } from 'react';
import Arrow from '../../resources/svg/Arrow';
import './blockInfoList.scss';
import { DataContext } from '../../hooks/DataContext';
import RoundSelect from '../roundSelect/RoundSelect';

import CodeInput from '../codeInput/CodeInput';

const BlockInfoList = ({ data, handleClick, activeDelete, activeEdit ,searchTerm }) => {
    const [openIndexes, setOpenIndexes] = useState({}); //      
    const [openContent, setOpenContent] = useState(false); //      
    const [selectedBlockId, setSelectedBlockId] = useState(null); //     
    const [selectedBlocks, setSelectedBlocks] = useState([]); //       activeDelete
   
    const toggleBlock = (index) => {
        setOpenIndexes(prevState => ({
            ...prevState,
            [index]: !prevState[index] //     
        }));
        setOpenContent((prev) => !prev);
    };
    const highlightText = (text, searchTerm) => {
        if (!searchTerm) return text; //    ,   
      
        //  ,       
        const safeSearchTerm = searchTerm.replace(/[.*+?^=!:${}()|\[\]\/\\]/g, "\\$&"); //   
        const regex = new RegExp(`(${safeSearchTerm})`, 'gi'); //    
      
        return text.split(regex).map((part, index) => 
          regex.test(part) ? <span key={index} className="highlight">{part}</span> : part
        );
      };
      
    //    
    const processText = (text) => {
        if (!text || typeof text !== 'string') return text;

        const regex = /<code>([\s\S]*?)<\/code>/g; //   
        const parts = [];
        let lastIndex = 0;

        let match;
        while ((match = regex.exec(text)) !== null) {
            const [fullMatch, codeContent] = match;

            //     
            if (match.index > lastIndex) {
                parts.push(highlightText(text.slice(lastIndex, match.index), searchTerm));
            }

            //     <code>
            parts.push(<CodeInput key={parts.length} code={codeContent} />);

            lastIndex = regex.lastIndex;
        }

        //      
        if (lastIndex < text.length) {
            parts.push(highlightText(text.slice(lastIndex), searchTerm));
        }

        return parts;
    };
    const handleSelectClick = (block, isSelected) => {
        
        if (activeEdit) {
            //  activeEdit,      
            if (isSelected) {
                setSelectedBlockId(null); //     ,  
            } else {
                setSelectedBlockId(block.id); //   
            }
        } else if (activeDelete) {
            //  activeDelete,     
            if (isSelected) {
                setSelectedBlocks(prevState => prevState.filter(id => id !== block.id)); //     
            } else {
                setSelectedBlocks(prevState => [...prevState, block.id]); //     
            }
        }
    
        handleClick(block, isSelected); //  handleClick,  
    };
    useEffect(() => {
        if (!activeDelete || !activeEdit) {
            setSelectedBlocks([]); 
            setSelectedBlockId(null);
        }
    }, [activeDelete, activeEdit]); // : activeDelete  activeEdit

    return (
        <div className="info">
            {data.map((block, index) => (
                <div
                    key={`block-wrapper-${block.id}`}
                    style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 20 }}
                >
                    {activeDelete || activeEdit ? (
                    <RoundSelect
                        block={block}
                        key={`round-select-${block.id}`}
                        isSelected={activeEdit ? selectedBlockId === block.id : selectedBlocks.includes(block.id)} //  activeEdit   ,  activeDelete  
                        handleClick={handleSelectClick} //   
                    />
                ) : null}
                    <div
                        className={`${activeDelete ? 'activeBlock' : ''} ${openIndexes[index] ? 'activeContent' : ''} info__block`}
                        key={`info-block-${block.id}`}
                        onClick={() => toggleBlock(index)}
                    >
                        <div className="info__content">
                            <p className="info__text">{processText(block.title)}</p>
                            <Arrow className={openIndexes[index] ? 'open' : 'closed'} />
                        </div>
                        {openIndexes[index] && (
                            <div className="info__details">
                                <hr style={{ marginBottom: 10, marginTop: 10 }} />
                                <p  style={{ whiteSpace: "pre-wrap" }}>{processText(block.text)}</p>
                            </div>
                        )}
                    </div>
                </div>
            ))}
        </div>
    );
};

export default BlockInfoList;
