import { useState, useRef, useEffect } from 'react';
import Plus from '../../resources/svg/Plus';
import './modal.scss';
import CodeInput from '../codeInput/CodeInput';

const Modal = ({ props }) => {
        const { type, setType, setTitle,title ,setText, text, addUser, setActive } = props;
        let types = ['React', 'ReactNative', 'JS', 'TS', 'SoftSkils', 'CSS', 'HTML', ''];
        const contentEditableRef = useRef(null);
        useEffect(() => {
            contentEditableRef.current.style.height = `${contentEditableRef.current.scrollHeight}px`;  //     

        }, [])
        const handleChange = (e) => {
            setText(e.target.value);
            contentEditableRef.current.style.height = `${contentEditableRef.current.scrollHeight}px`;  //     
        };

        const handleCodeInsert = (text) => {
            // ,  text     
            if (typeof text !== 'string' || text.trim() === '') {
                return null;
            }
        
            const codeRegex = /<code>([\s\S]*?)<\/code>/g; //     <code></code>
            const matches = [...text.matchAll(codeRegex)];  //   
        
            if (matches.length > 0) {
                return matches[0][1]; //      <code>
            }
        
            return null; //   
        };
        
    
        // ,      CodeInput
        const codeToInsert = handleCodeInsert(text);

    return (
        <div className="modal">
        <div className="modal__block">
            <div className="modal__type">
                {types.map((item) => (
                    <div 
                        className={`modal__typeBlock ${type === item ? 'selected' : ''}`} 
                        onClick={() => setType(item)} 
                        key={item}
                    >
                        <p>{item}</p>
                    </div>
                ))}
            </div>

            <div className="modal__title">
                <input 
                    className='modal__titleText' 
                    onChange={(e) => setTitle(e.target.value)} 
                    placeholder=''
                    value={title}
                />
            </div>

            <div className="modal__info">
                <textarea 
                    className='modal__infoText' 
                    onChange={handleChange} 
                    placeholder='' 
                    value={text} 
                    ref={contentEditableRef}

                />
                
                {/*  CodeInput,       <code></code> */}
                {codeToInsert && <CodeInput code={codeToInsert} />}
            </div>

            <div className='modal__btns'>
                <button className='modal__btnsBtn modal__close' onClick={() => setActive(false)}>
                    
                </button>
                <button className='modal__btnsBtn modal__add' onClick={() => addUser()}>
                    
                </button>
            </div>
        </div>
    </div>
    );
};

export default Modal;
